import React from "react";
import Seperator from "../../common/seperator/index";
import SocialContact from "../../common/social contact/index";
import "./contact.css";

function Contact() {
    return (
        <div className="contact">
        <Seperator/>
        <label className="section-title">Contacts</label>
        <div className="contact-container">
            <div className="contact-only">
                <p>If you want to get in touch, please contact me in any of the platform</p>
                <SocialContact/>
            </div>
        </div>
        </div>
    )
}

export default Contact
