import React from "react";
import SocialContact from "../../common/social contact/index";
import "./about.css";

function About() {
    return (
        <div className="about">
            <div className="about-top">
                <div className="about-info">
                    Hi There! This is 
                    <br/><span className="info-name">Debangsh Bose.</span> 
                    <br/> I am an Engineering student, currently persuing B.Tech on Information Technology and I love creating web designs. You can get me as involved as you want me to be in your project and I can promise you the the extent level of dedication to the work.
                </div>
                <div className="about-photo">
                    <img src={require('../../../assets/Debangsh.jpg').default} className='picture' alt="" />
                </div>
            </div>
            <SocialContact/>
        </div>
    )
}

export default About
